/**
* Container
*/
.container,
.container-fluid {
  width: 100%;
  margin-right: auto;
  margin-left: auto;
  padding-right: var(--spacing);
  padding-left: var(--spacing);
}

@media (min-width: 576px) {
  .container {
    max-width: 510px;
    padding-right: 0;
    padding-left: 0;
  }
}
@media (min-width: 768px) {
  .container {
    max-width: 700px;
  }
}
@media (min-width: 992px) {
  .container {
    max-width: 920px;
  }
}
@media (min-width: 1200px) {
  .container {
    max-width: 1130px;
  }
}

/**
* Grid
* Minimal grid system with auto-layout columns
*/
.grid {
  grid-column-gap: var(--grid-spacing-horizontal);
  grid-row-gap: var(--grid-spacing-vertical);
  display: grid;
  grid-template-columns: 1fr;
  margin: 0;
}
@media (min-width: 992px) {
  .grid {
    grid-template-columns: repeat(auto-fit, minmax(0%, 1fr));
  }
}
.grid > * {
  min-width: 0;
}

/**
 * Button
 */
button {
  margin: 0;
  overflow: visible;
  background-color: aqua;
  font-family: inherit;
  text-transform: none;
}

.card {
  margin: 3px;
  padding: 15px;
  border-radius: 10px;
  background-color: #F5F6F7;
}
.card .card-header {
  font-weight: bold;
}
.card .card-body {
  background-color: hsl(205deg, 12%, 59%);
}
.nav {
  display: flex;
  top: 0;
  left: 0;
  justify-content: space-between;
  width: 100%;
  padding: 15px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}
.nav ul {
  display: flex;
  column-gap: 20px;
  align-items: center;
  list-style: none;
}
.nav ul a {
  transition: all 0.2s linear;
}
.nav ul a .logo {
  font-weight: 500;
  font-size: 22px;
}

